////////////////////////////////
// custom reset
////////////////////////////////

// Margin + padding
* {
	margin: 0;
	padding: 0;
}

// Make it responsive
img, textarea, svg {
	max-width: 100%;
}

// Enable svg overflow
svg {
	overflow: visible;
}

// Border box default, courtesy of Paul Irish.
// http://www.paulirish.com/2012/box-sizing-border-box-ftw/
html {
	box-sizing: border-box;
}
*, *:before, *:after {
	box-sizing: inherit;
}

// Pos:rel all the things
body * {
	position: relative;
}

// Hide horizontal scroll bars. Just in case.
html, body {
	width: 100%;
	max-width: 100%;
}

// Forms are stupid
label {
	display: inline-block;
}
input, textarea {
	display: block;
	width: 100%;
}
input[type=checkbox],
input[type=radio],
svg {
	vertical-align: middle;
}

// Let users resize textareas
textarea {
	resize: vertical;
}

// Reset buttons
button {
	overflow: visible;
	background: transparent;
	border: none;
	appearance: button;
	display: inline-block;
	box-shadow: none;

	&::-moz-focus-inner {
		border: 0
	}
}

// Reset list-style
ul, ol {
	list-style: none;
}

// Add IE support for hidden attribute
[hidden] {
	display: none;
}

// Disabled elements
[disabled] {
	cursor: default;
}

////////////////////////////////
// Cherry picked from normalize
////////////////////////////////

// Prevent iOS text size adjust after orientation change,
// without disabling user zoom.
html {
	-ms-text-size-adjust: 100%;
	-webkit-text-size-adjust: 100%;
}

// Display: block defaults
article, aside, details, figcaption, figure, img, // img added by me
footer, header, hgroup, main, menu, nav, section, summary {
	display: block;
}

// Remove linked image border
img {
	border: 0;
}

// More form fixes
button, input, optgroup, select, textarea {
	color: inherit; /* 1 */
	font: inherit; /* 2 */
}

// IE overflow bug
button {
	overflow: visible;
}

// Address inconsistent text-transform inheritance
button, select {
	text-transform: none;
}

// Firefox fixes
button::-moz-focus-inner,
input::-moz-focus-inner {
	border: 0;
}
input {
	line-height: normal;
}

// Safari + chrome fixes
input[type="search"] {
	-webkit-appearance: textfield;
}
input[type="search"]::-webkit-search-cancel-button,
input[type="search"]::-webkit-search-decoration {
	-webkit-appearance: none;
}

// Fieldset
fieldset {
	border: none;
}

// IE scrollbar
textarea {
	overflow: auto;
}

// Tables
table {
	border-collapse: collapse;
	border-spacing: 0;
}

////////////////////////////////
// Accessibility
////////////////////////////////

@media (prefers-reduced-motion: reduce) {
	* {
		transition-duration: 0.01s !important;
		animation-duration: 0.01s !important;
	}
}